<template>
  <div
    class="header-wrap"
    :style="{
      background: currentPath == '/index' ? 'rgba(0,0,0,0)' : 'rgb(17, 16, 17)'
    }"
  >
    <div class="logo">
      <img src="http://robweld.zjnu.edu.cn/zsd/img/zsd-logo.775d192e.png" />
    </div>
    <div class="menus">
      <div
        class="menu"
        :class="item.path === currentPath ? 'active' : ''"
        v-for="(item, index) in menus"
        :key="index"
      >
        <span class="title" @click="menuChange(item)">{{
          item.meta.title
        }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data() {
    return {
      menus: [
        {
          path: '/index',
          name: 'index',
          meta: {
            title: '首页'
          }
        },
        {
          path: '/description',
          name: 'description',
          meta: {
            title: '实验描述'
          }
        },
        {
          path: '/hallmark',
          name: 'hallmark',
          meta: {
            title: '实验特色'
          }
        },
        {
          path: '/data',
          name: 'data',
          meta: {
            title: '实验数据'
          }
        },
        {
          path: '/servicePlan',
          name: 'servicePlan',
          meta: {
            title: '服务计划'
          }
        },
        {
          path: '/serviceTeam',
          name: 'serviceTeam',
          meta: {
            title: '服务团队'
          }
        },
        {
          path: '/applyDoc',
          name: 'applyDoc',
          meta: {
            title: '申报书'
          }
        },
        {
          path: '/out',
          name: 'out',
          meta: {
            title: '退出登录'
          }
        }
      ]
    }
  },
  computed: {
    currentPath() {
      return this.$route.path
    }
  },
  methods: {
    // 切换菜单
    menuChange(item) {
      if (item.name === 'out') {
        const that = this
        this.$confirm('此操作将退出登录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          that.$router.replace('/login')
        })
        return
      }
      this.$router.push(item.path)
    }
  }
}
</script>

<style lang="less" scoped>
.header-wrap {
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  z-index: 2;
  .logo {
    height: 80px;
    display: flex;
    align-items: center;
    margin-left: 8%;
    img {
      width: 212px;
    }
  }
  .menus {
    height: 80px;
    display: flex;
    align-items: center;
    margin-right: 4%;
    .menu {
      padding: 0 16px;
      &.active {
        position: relative;
        &::before {
          content: ' ';
          width: 100%;
          height: 2px;
          background: white;
          position: absolute;
          bottom: -20px;
          left: 0;
        }
      }
      .title {
        color: white;
        font-size: 20px;
        cursor: pointer;
      }
    }
  }
}
</style>
