import Vue from 'vue'
import VueRouter from 'vue-router'
import BasicLayout from '@/layouts/BasicLayout.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: BasicLayout,
    redirect: '/index',
    children: [
      {
        path: '/index',
        name: 'index',
        component: () => import('@/views/Index/Index.vue'),
        meta: {
          title: '首页'
        }
      },
      {
        path: '/description',
        name: 'description',
        component: () => import('@/views/Description/Description.vue'),
        meta: {
          title: '实验描述'
        }
      },
      {
        path: '/hallmark',
        name: 'hallmark',
        component: () => import('@/views/Hallmark/Hallmark.vue'),
        meta: {
          title: '实验特色'
        }
      },
      {
        path: '/data',
        name: 'data',
        component: () => import('@/views/Data/Data.vue'),
        meta: {
          title: '实验数据'
        }
      },
      {
        path: '/servicePlan',
        name: 'servicePlan',
        component: () => import('@/views/ServicePlan/ServicePlan.vue'),
        meta: {
          title: '服务计划'
        }
      },
      {
        path: '/serviceTeam',
        name: 'serviceTeam',
        component: () => import('@/views/ServiceTeam/ServiceTeam.vue'),
        meta: {
          title: '服务团队'
        }
      },
      {
        path: '/applyDoc',
        name: 'applyDoc',
        component: () => import('@/views/ApplyDoc/ApplyDoc.vue'),
        meta: {
          title: '申报书'
        }
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('@/views/User/Login.vue')
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
